$blue-darken-4: #367fa9;
$blue-darken-3: #3c8dbc;
$blue-darken-2: #00c0ef;
$red-lighten-4: #f56954;
$white-darken-3: #f0f0f0;

// color selectors
.blue.darken-4{
	background-color: $blue-darken-4  !important;
}
.blue.darken-3{
	background-color: $blue-darken-3 !important;
}
.blue.darken-2{
	background-color: $blue-darken-2 !important;
}
.red.lighten-4{
	background-color: $red-lighten-4 !important;
}
.red-text.text-lighten-4{
	color: $red-lighten-4 !important;
}
.white.darken-3{
	background-color: $white-darken-3 !important;
}
.white-text.darken-3-text{
	color: $white-darken-3 !important;
}

// height configuration
.tall-line{
	line-height: 2.5rem;
}

// css tweaks
.nf-container .col{
	float: none;
}
.nf-container .card-container{
	display: inline-block;
}
.nrp{
	padding-right: 0px !important;
}
.nlp{
	padding-left: 0px !important;
}
.mt30{
	margin-top: 30px;
}
.btn.btn-primary {
	background-color: #3c8dbc;
	border-color: #367fa9;
}
.btn.btn-default {
	background-color: #fafafa;
	color: #666;
	border-color: #ddd;
	border-bottom-color: #ddd;
}
.table.bordered{
	border: 1px solid #ddd;
}
.table.bordered th,
.table.bordered td{
	border: 1px solid #ddd;
	padding: 8px;
	line-height: 1.5rem;
	vertical-align: top;
}
.table.bordered th{
	border-bottom-width: 2px;
}
nav ul li.no-hover:hover{
	background: none;
}
.modal-close i{
	color: #000;
	opacity: .2;
	font-weight: bold;
	font-size: 1.5rem;
}
.menu-sidebar-collapse {
	margin: 0px 10px 0 18px;
}
.menu-sidebar-collapse i{
	line-height: 36px !important;
}
h1.logo-wrapper{
	margin: 0px 0px;
}
h1 span.logo-text{
	color: #ECF0F9;
	font-size: 3rem;
	line-height: .5;
}

header .brand-logo {
	margin: 1px 0px;
	padding: 0 20px;
	padding-left: 0px;
}

// select2
.select2-container.browser-default{
	width: 100%;
}
.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single, .select2-container--default.select2-container--focus .select2-selection--multiple{
	height: 40px;
	border: none;
	border-bottom: 1px solid #9e9e9e;
	border-radius: 0;
	outline: 0;
	background-color: transparent;
}
.select2-container--default .select2-selection--single .select2-selection__rendered{
	line-height: 40px;
	padding-left: 0;
}
.select2-container--default .select2-selection--single .select2-selection__arrow{
	height: 40px;
}
.select2-container--default .select2-search--dropdown .select2-search__field{
	border-top: none;
	border-right: none;
	border-left: none;
}
.select2-container--default .select2-results__option--highlighted[aria-selected]{
	background-color: #42A5F5;
}
.select2-results__option{
	padding: 1rem;
}
// end of select2 

// sidebar navigation
.left-sidebar-nav {
	position: fixed;
	width: 100px;
	left: 180px;
	z-index: 999;
	height: auto;
}
#sidenav-overlay {
	background-color: transparent;
}
ul.side-nav.leftside-navigation {
	top: 64px;
	overflow: hidden;
}
ul.side-nav li {
	padding: 0;
}
ul.side-nav.leftside-navigation li {
	line-height: 44px;
}
.side-nav a {
	line-height: 41px;
	height: 42px;
}
li.li-hover:hover{
	background: transparent !important;
}
ul.side-nav.leftside-navigation li a {
	font-size: 14px;
	font-weight: 400;
}
ul.side-nav.leftside-navigation li:hover,
ul.side-nav.leftside-navigation li.active {
	background-color: rgba(0, 0, 0, 0.05);
}
ul.side-nav.leftside-navigation li.no-hover:hover{
	background-color: transparent;
}

#slide-out li a i {
	line-height: inherit;
	width: 2rem;
	font-size: 1.6rem;
	display: block;
	float: left;
	text-align: center;
	margin-right: 1rem;
}
ul.side-nav.leftside-navigation li.user-details {
	background: url(../images/user-bg.jpg) no-repeat center center;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	margin-bottom: 15px;
	padding: 15px 0px 0px 15px;
}
.user-details .row {
	margin: 0;
}

#slide-out li a i {
	line-height: inherit;
	width: 2rem;
	font-size: 1.6rem;
	display: block;
	float: left;
	text-align: center;
	margin-right: 1rem;
}
ul.side-nav.leftside-navigation .profile-btn {
	margin: 0;
	text-transform: capitalize;
	padding: 0;
	text-shadow: 1px 1px 1px #444;
	font-size: 15px;
}

#profile-dropdown li a{
	padding: 0px 5px;
}
.user-role {
	color: #fff;
	margin-top: -16px;
	font-size: 13px;
	text-shadow: 1px 1px 1px #444;
}
.side-nav .tiny{
	position:relative;
	top: 2px;
}
.collection-header {
	font-size: 1.64rem;
	font-weight: 500;
}
.collection .collection-item.avatar i.circle {
	font-size: 28px;
}
p.collections-title {
	font-size: 1.0rem;
	padding: 0;
	margin: 0;
	font-weight: 500;
}

p.collections-content {
	font-size: 0.9rem;
	padding: 0;
	margin: 0;
	font-weight: 400;
}

.service-cat {
	padding: 2px 4px;
	color: #fff;
	font-weight: 300;
	font-size: 0.8rem;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	border-radius: 2px;
	background-clip: padding-box;
}

.stats .card-action a{
	margin-right: 0px;
}
ul.side-nav .collapsible-header {
	margin: 0;
}
#right-search .input-field{
	margin-top: 0px;
}
#chat-out .collapsible-header{
	border: none;
	line-height: 45px;
	height: 45px;
	font-weight: 400;
}
#chat-out .chat-out-list {
	padding: 5px;
	margin: 0;
	border-bottom: 1px solid #e0e0e0;
}
#chat-out .recent-activity-list-icon {
	margin-top: 8px;
	font-size: 2rem;
}
#chat-out .recent-activity-list-text a {
	font-size: 0.8rem;
	padding: 0;
	font-weight: 600;
}
#chat-out .recent-activity-list-text p {
	font-size: 0.9rem;
	padding: 0;
}
.tab-option{
	margin-bottom: 0px;
	margin-left: 5px;
}
.tab-option>li {
	float: left;
	margin-bottom: -1px;
}
.tab-option a{
	color: #555;
	cursor: default;
	background-color: #fff;
	border: 1px solid #ddd;
	border-bottom-color: transparent;
	position: relative;
	display: block;
	padding: 10px 15px;
}
.review-block {
	position: relative;
	margin: 0px 0;
	padding: 39px 19px 14px;
	background-color: white;
	border: 1px solid #DDD;
	font-size:0.9rem;
	padding-left: 5px;
}
.pagi-container .dataTables_paginate{
	float: right;
}
.pagi-container .pagination{
	display: inline-block;
	margin: 0px;
}
.pagination li {
	float: none;
	font-size: .9rem;
	padding: 0px;
	line-height: 1.4rem;
	border-radius: 0px;
	text-align: center;
	display: inline-block;
}
// other configuration
.clearfix:before, .clearfix:after {
	display: table;
	content: " ";
}
.clearfix:after {
	clear: both;
}
body {
	display: flex;
	min-height: 100vh;
	flex-direction: column;
}

main {
	flex: 1 0 auto;
}

footer.page-footer{
	padding-top: 0px;
}
